	module.exports = {"pathContext":{"docHtml":"<h1>normalize-path <a href=\"https://www.npmjs.com/package/normalize-path\"><img src=\"https://img.shields.io/npm/v/normalize-path.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/normalize-path\"><img src=\"https://img.shields.io/npm/dm/normalize-path.svg?style=flat\" alt=\"NPM monthly downloads\"></a>  <a href=\"https://npmjs.org/package/normalize-path\"><img src=\"https://img.shields.io/npm/dt/normalize-path.svg?style=flat\" alt=\"NPM total downloads\"></a> <a href=\"https://travis-ci.org/jonschlinkert/normalize-path\"><img src=\"https://img.shields.io/travis/jonschlinkert/normalize-path.svg?style=flat&#x26;label=Travis\" alt=\"Linux Build Status\"></a></h1>\n<blockquote>\n<p>Normalize file path slashes to be unix-like forward slashes. Also condenses repeat slashes to a single slash and removes and trailing slashes unless disabled.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install --save normalize-path\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var normalize = require('normalize-path');\n\nnormalize('\\\\foo\\\\bar\\\\baz\\\\');\n//=> '/foo/bar/baz'\n\nnormalize('./foo/bar/baz/');\n//=> './foo/bar/baz'\n</code></pre>\n<p>Pass <code>false</code> as the last argument to <strong>keep</strong> trailing slashes:</p>\n<pre><code class=\"language-js\">normalize('./foo/bar/baz/', false);\n//=> './foo/bar/baz/'\n\nnormalize('foo\\\\bar\\\\baz\\\\', false);\n//=> 'foo/bar/baz/'\n</code></pre>\n<h2>About</h2>\n<h3>Related projects</h3>\n<ul>\n<li><a href=\"https://www.npmjs.com/package/contains-path\">contains-path</a>: Return true if a file path contains the given path. | <a href=\"https://github.com/jonschlinkert/contains-path\" title=\"Return true if a file path contains the given path.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/ends-with\">ends-with</a>: Returns <code>true</code> if the given <code>string</code> or <code>array</code> ends with <code>suffix</code> using strict equality for… <a href=\"https://github.com/jonschlinkert/ends-with\">more</a> | <a href=\"https://github.com/jonschlinkert/ends-with\" title=\"Returns &#x60;true&#x60; if the given &#x60;string&#x60; or &#x60;array&#x60; ends with &#x60;suffix&#x60; using strict equality for comparisons.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-absolute\">is-absolute</a>: Polyfill for node.js <code>path.isAbolute</code>. Returns true if a file path is absolute. | <a href=\"https://github.com/jonschlinkert/is-absolute\" title=\"Polyfill for node.js &#x60;path.isAbolute&#x60;. Returns true if a file path is absolute.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-relative\">is-relative</a>: Returns <code>true</code> if the path appears to be relative. | <a href=\"https://github.com/jonschlinkert/is-relative\" title=\"Returns &#x60;true&#x60; if the path appears to be relative.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/parse-filepath\">parse-filepath</a>: Pollyfill for node.js <code>path.parse</code>, parses a filepath into an object. | <a href=\"https://github.com/jonschlinkert/parse-filepath\" title=\"Pollyfill for node.js &#x60;path.parse&#x60;, parses a filepath into an object.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/path-ends-with\">path-ends-with</a>: Return <code>true</code> if a file path ends with the given string/suffix. | <a href=\"https://github.com/jonschlinkert/path-ends-with\" title=\"Return &#x60;true&#x60; if a file path ends with the given string/suffix.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/path-segments\">path-segments</a>: Get n specific segments of a file path, e.g. first 2, last 3, etc. | <a href=\"https://github.com/jonschlinkert/path-segments\" title=\"Get n specific segments of a file path, e.g. first 2, last 3, etc.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/rewrite-ext\">rewrite-ext</a>: Automatically re-write the destination extension of a filepath based on the source extension. e.g… <a href=\"https://github.com/jonschlinkert/rewrite-ext\">more</a> | <a href=\"https://github.com/jonschlinkert/rewrite-ext\" title=\"Automatically re-write the destination extension of a filepath based on the source extension. e.g  &#x60;.coffee&#x60; => &#x60;.js&#x60;. This will only rename the ext, no other path parts are modified.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/unixify\">unixify</a>: Convert Windows file paths to unix paths. | <a href=\"https://github.com/jonschlinkert/unixify\" title=\"Convert Windows file paths to unix paths.\">homepage</a></li>\n</ul>\n<h3>Contributing</h3>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"../../issues/new\">please create an issue</a>.</p>\n<h3>Contributors</h3>\n<table>\n<thead>\n<tr>\n<th><strong>Commits</strong></th>\n<th><strong>Contributor</strong></th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>31</td>\n<td><a href=\"https://github.com/jonschlinkert\">jonschlinkert</a></td>\n</tr>\n<tr>\n<td>1</td>\n<td><a href=\"https://github.com/phated\">phated</a></td>\n</tr>\n</tbody>\n</table>\n<h3>Building docs</h3>\n<p><em>(This project's readme.md is generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb</a>, please don't edit the readme directly. Any changes to the readme must be made in the <a href=\".verb.html\">.verb.md</a> readme template.)</em></p>\n<p>To generate the readme, run the following command:</p>\n<pre><code class=\"language-sh\">$ npm install -g verbose/verb#dev verb-generate-readme &#x26;&#x26; verb\n</code></pre>\n<h3>Running tests</h3>\n<p>Running and reviewing unit tests is a great way to get familiarized with a library and its API. You can install dependencies and run tests with the following command:</p>\n<pre><code class=\"language-sh\">$ npm install &#x26;&#x26; npm test\n</code></pre>\n<h3>Author</h3>\n<p><strong>Jon Schlinkert</strong></p>\n<ul>\n<li><a href=\"https://github.com/jonschlinkert\">github/jonschlinkert</a></li>\n<li><a href=\"https://twitter.com/jonschlinkert\">twitter/jonschlinkert</a></li>\n</ul>\n<h3>License</h3>\n<p>Copyright © 2017, <a href=\"https://github.com/jonschlinkert\">Jon Schlinkert</a>.\nReleased under the <a href=\"LICENSE\">MIT License</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a>, v0.4.3, on March 29, 2017.</em></p>","docPath":"argo-ci/node_modules/normalize-path/readme","proj":"argo-ci"}}

